.wp-block-eedee-block-gutenslider.adaptive-height {
	.swiper-wrapper {
		min-height: unset;
	}

	.swiper-wrapper.on-first-slide {
		height: unset !important;
	}

	.wp-block-eedee-block-gutenslide {
		height: unset;
		width: 100%;
		align-self: center;
	}

	.wp-block-eedee-block-gutenslide .slide-content {
		min-height: unset;
	}

	.eedee-background-div {
		height: 100%;
    position: relative;

		> img {
			width: 100%;
			height: 100%;
		}

		> video {
			width: 100%;
			height: 100%;
		}
	}
}
