// slider navigation
@import '../../variables.scss';

//gutenberg editor marker
#start-resizable-editor-section {
	display: none;
}

.wp-block-eedee-block-gutenslider {
	.swiper-vertical ~ .eedee-gutenslider-next {
		transform: rotate3d(0,0,1,90deg);
	}
	.swiper-vertical ~ .eedee-gutenslider-prev {
		transform: rotate3d(0,0,1,90deg);
	}

	.eedee-gutenslider-nav {
		display: block;
		cursor: pointer;
		border: none;
		outline: none;
		position: absolute;
		z-index: 20;
		padding: 0;
		transform: translate(0);
		background: transparent !important;
		width: var(--gutenslider-arrow-size-sm);
		height: var(--gutenslider-arrow-size-sm);

		&.swiper-button-disabled {
			opacity: 0.1;
			cursor: default;

			&:hover {
				.bg,
				#bg {
					background: var(--gutenslider-arrow-bg-color);
					fill: var(--gutenslider-arrow-bg-color);
				}

				.arrow,
				#arrow {
					stroke: none;
					fill: var(--gutenslider-arrow-color);
				}
			}
		}

		svg {
			width: var(--gutenslider-arrow-size-sm);
			height: var(--gutenslider-arrow-size-sm);
			fill: transparent;
			stroke: transparent;

			.bg,
			#bg {
				background: var(--gutenslider-arrow-bg-color);
				fill: var(--gutenslider-arrow-bg-color);
			}

			.arrow,
			#arrow {
				stroke: none;
				fill: var(--gutenslider-arrow-color);
			}

			&:hover {
				.bg,
				#bg {
					background: var(--gutenslider-arrow-bg-hover-color);
					fill: var(--gutenslider-arrow-bg-hover-color);
				}

				.arrow,
				#arrow {
					stroke: none;
					fill: var(--gutenslider-arrow-hover-color);
				}
			}
		}
	}

	&.arrows-sm .eedee-gutenslider-nav {
		display: block;
	}

	&.arrows-sm-none .eedee-gutenslider-nav {
		display: none;
	}

	&.arrow-position-top-bottom {
		.eedee-gutenslider-prev {
			top: var(--gutenslider-arrow-y-offset-sm);
			transform: translateX(-50%);
			left: calc(50% + var(--gutenslider-arrow-x-offset-sm));
		}

		.swiper-vertical ~ .eedee-gutenslider-prev {
			transform: translateX(-50%) rotate3d(0,0,1,90deg);
		}

		.eedee-gutenslider-next {
			bottom: var(--gutenslider-arrow-y-offset-sm);
			transform: translateX(-50%);
			left: calc(50% + var(--gutenslider-arrow-x-offset-sm));
		}

		.swiper-vertical ~ .eedee-gutenslider-next {
			transform: translateX(-50%) rotate3d(0,0,1,90deg);
		}
	}

	&.arrow-position-sides {
		.eedee-gutenslider-prev {
			top: calc(50% + var(--gutenslider-arrow-y-offset-sm));
			transform: translateY(-50%);
			left: var(--gutenslider-arrow-x-offset-sm);;
		}

		.swiper-vertical ~ .eedee-gutenslider-prev {
			transform: translateY(-50%) rotate3d(0,0,1,90deg);
		}

		.eedee-gutenslider-next {
			top: calc(50% + var(--gutenslider-arrow-y-offset-sm));
			transform: translateY(-50%);
			right: var(--gutenslider-arrow-x-offset-sm);
		}

		.swiper-vertical ~ .eedee-gutenslider-next {
			transform: translateY(-50%) rotate3d(0,0,1,90deg);
		}
	}

	&.arrow-position-top-left {
		.eedee-gutenslider-prev {
			top: calc(var(--gutenslider-arrow-y-offset-sm) + 10px);
			left: var(--gutenslider-arrow-x-offset-sm);
		}

		.eedee-gutenslider-next {
			top: calc(var(--gutenslider-arrow-y-offset-sm) + 10px);
			left: calc(var(--gutenslider-arrow-x-offset-sm) + var(--gutenslider-arrow-size-sm) + var(--gutenslider-arrow-x-spacing));
		}
	}

	&.arrow-position-top-right {
		.eedee-gutenslider-prev {
			left: auto;
			top: calc(var(--gutenslider-arrow-y-offset-sm) + 10px);
			right: calc(var(--gutenslider-arrow-x-offset-sm) + var(--gutenslider-arrow-size-sm) + var(--gutenslider-arrow-x-spacing));
		}

		.eedee-gutenslider-next {
