.wp-block-eedee-block-gutenslider {
	&.arrow-mb-difference {
		.eedee-gutenslider-nav {
			mix-blend-mode: difference;
		}
	}

	&.arrow-mb-color-burn {
		.eedee-gutenslider-nav {
			mix-blend-mode: color-burn;
		}
	}

	&.arrow-mb-color-dodge {
		.eedee-gutenslider-nav {
			mix-blend-mode: color-dodge;
		}
	}

	&.arrow-mb-exclusion {
		.eedee-gutenslider-nav {
			mix-blend-mode: exclusion;
		}
	}

	&.arrow-mb-saturation {
		.eedee-gutenslider-nav {
			mix-blend-mode: saturation;
		}
	}

	&.arrow-follow-mouse {
		.eedee-gutenslider-prev {
			top: 50%;
			transform: translateY(-50%);
			left: 0;
		}

		.swiper-container-vertical ~ .eedee-gutenslider-prev {
			transform: translateY(-50%) rotate3d(0,0,1,90deg);
		}

		.eedee-gutenslider-next {
			top: 50%;
			transform: translateY(-50%);
			right: 0;
		}

		.swiper-container-vertical ~ .eedee-gutenslider-next {
			transform: translateY(-50%) rotate3d(0,0,1,90deg);
		}
		@media (hover: hover) and (pointer: fine) {
			.eedee-gutenslider-next svg {
				transform: scale(0) translateX(-100%);
				transform-origin: 50% 50%;
			}

			.eedee-gutenslider-prev svg {
				transform: scale(0) translateX(100%);
				transform-origin: 50% 50%;
			}

			.eedee-gutenslider-nav {
				transition: tranform 0.3s;
				overflow: hidden;
				opacity: 0;

				svg {
					transition: opacity 0.3s, transform 0.3s;
				}
			}

			.eedee-gutenslider-nav:not(.ed-active) {
				opacity: 0 !important;
			}

			.eedee-gutenslider-nav.ed-active {
				transition: opacity 0.3s;
				opacity: 1;

				svg {
					transform: scale(1) translateX(0);
				}
			}
		}
	}
}
